@-webkit-keyframes text-slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }

  70% {
    -webkit-transform: translateY(clamp(2px, 1vw, 8px));
            transform: translateY(clamp(2px, 1vw, 8px));
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes text-slide-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }

  70% {
    -webkit-transform: translateY(clamp(2px, 1vw, 8px));
            transform: translateY(clamp(2px, 1vw, 8px));
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes color-change-header {
  0% {
    color: white;
  }

  100% {
    color: var(--color-orange);
  }
}

@keyframes color-change-header {
  0% {
    color: white;
  }

  100% {
    color: var(--color-orange);
  }
}

